import { initializeApp, getApps } from "firebase/app"
import { getFirestore, collection, query, orderBy, onSnapshot, Timestamp } from "firebase/firestore"
import type { LocationData } from "./types"

const firebaseConfig = {
  apiKey: process.env.NEXT_PUBLIC_FIREBASE_API_KEY,
  authDomain: process.env.NEXT_PUBLIC_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.NEXT_PUBLIC_FIREBASE_PROJECT_ID,
  storageBucket: process.env.NEXT_PUBLIC_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.NEXT_PUBLIC_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.NEXT_PUBLIC_FIREBASE_APP_ID,
  measurementId: process.env.NEXT_PUBLIC_FIREBASE_MEASUREMENT_ID,
}

// Initialize Firebase (singleton pattern)
const app = getApps().length === 0 ? initializeApp(firebaseConfig) : getApps()[0]
const db = getFirestore(app)

export { app, db }

export function getRealtimeLocations(
  collectionName: string,
  callback: (locations: LocationData[]) => void,
): () => void {
  const locationsRef = collection(db, collectionName)
  const q = query(locationsRef, orderBy("timestamp", "asc"))

  const unsubscribe = onSnapshot(
    q,
    (snapshot) => {
      const locations: LocationData[] = []
      snapshot.forEach((doc) => {
        const data = doc.data()
        locations.push({
          id: doc.id,
          latitude: data.latitude,
          longitude: data.longitude,
          timestamp: data.timestamp instanceof Timestamp ? data.timestamp.toDate() : new Date(data.timestamp),
          speed: data.speed,
          accuracy: data.accuracy,
        })
      })
      callback(locations)
    },
    (error) => {
      console.error("[v0] Firestore error:", error)
    },
  )

  return unsubscribe
}
