"use client"

interface RouteInfoProps {
  routeName: string
  startTime?: string
  endTime?: string
}

export function RouteInfo({ routeName, startTime, endTime }: RouteInfoProps) {
  return (
    <div className="fixed bottom-0 left-0 right-0" style={{ zIndex: 9999 }}>
      <div className="bg-white/95 rounded-t-3xl shadow-lg px-6 py-4">
        <div className="flex items-center justify-between">
          <div>
            <div className="text-xl font-bold text-gray-900">{routeName}</div>
            {startTime && endTime && (
              <div className="text-sm text-gray-600 mt-1">
                運行時間: {startTime} - {endTime}
              </div>
            )}
          </div>
          <div className="w-3 h-3 rounded-full bg-green-500 animate-pulse" />
        </div>
      </div>
    </div>
  )
}
