"use client"

interface PassengerCountProps {
  count?: number
}

export function PassengerCount({ count }: PassengerCountProps) {
  return (
    <div className="fixed top-4 left-4" style={{ zIndex: 9999 }}>
      <div className="bg-white/95 rounded-2xl shadow-lg px-4 py-3 flex items-center gap-3">
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="#364153"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <path d="M17 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2" />
          <circle cx="9" cy="7" r="4" />
          <path d="M23 21v-2a4 4 0 0 0-3-3.87" />
          <path d="M16 3.13a4 4 0 0 1 0 7.75" />
        </svg>
        <div>
          <div className="text-xs text-gray-600">現在の混雑状況</div>
          <div className="text-lg font-bold text-gray-900">{count !== undefined ? `${count}人` : "取得中..."}</div>
        </div>
      </div>
    </div>
  )
}
