"use client"

import type React from "react"

import { useEffect, useState } from "react"
import { createPortal } from "react-dom"

interface UIOverlayPortalProps {
  children: React.ReactNode
}

export function UIOverlayPortal({ children }: UIOverlayPortalProps) {
  const [mounted, setMounted] = useState(false)

  useEffect(() => {
    setMounted(true)
    console.log("[v0] UIOverlayPortal mounted")
    return () => setMounted(false)
  }, [])

  if (!mounted) return null

  return createPortal(
    <div
      className="fixed inset-0 pointer-events-none"
      style={{
        zIndex: 10000,
        backgroundColor: "rgba(255, 0, 0, 0.3)", // デバッグ用：赤い半透明背景
        border: "5px solid yellow", // デバッグ用：黄色い枠
      }}
    >
      {children}
    </div>,
    document.body,
  )
}
